/* ~~~ ACCESSIBILITY CLASSES ~~~ */

/* Skip Link */

.skip-link {
  display: inline-block;
  padding: 1.25rem 1rem;
  outline: 3px solid;
  // border-radius: 0.25rem;
  // background: var(--color-highlight);
  color: var(--ltdk-clr-txt);
  text-decoration: none;
  font-weight: 700;
  text-transform: uppercase;
  position: absolute;
  top: 1.5rem;
  left: 1.5rem;
}

.skip-link:hover {
  background: var(--ltdk-clr-hlt);
  color: var(--ltdk-clr-hlt-txt);
  outline: none;
}

.skip-link:not(:focus) {
  /* Visually hides the link when it is not focused. Screen readers can still "see" it. */

  border: 0;
  clip: rect(0 0 0 0);
  height: auto;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}