/* <button> Element Reset */
/* TODO: Complete <button> element module styles. */

button {
    display: inline-block;
    border: none;
    margin: 0;
    padding: 0;
    font-family: var(--font-sans);
    font-size: 1rem;
    line-height: 1;
    background: transparent;
    -webkit-appearance: none;
}

