/*  ~~~~~~~~~~~~~~~~~~
    ~~~ Navigation ~~~ 
    ~~~~~~~~~~~~~~~~~~  */

/* PRIMARY NAVIGATION */

.nav--primary {
  box-sizing: border-box;
}

.nav--primary .active {
  background-color: light-dark(var(--color-bg-dark), var(--color-bg-light));
  border-color: light-dark(var(--color-border-dark), var(--color-border-light));
  color: light-dark(var(--color-text-dark), var(--color-text-light));
}

.nav__list {
  margin: 0;
  padding: 0;
  gap: 1rem;
  justify-content: center;
}

.nav__list-item {
  flex: 0 1 auto;

  // Dimensions
  width: 14ch;

  // Main colors
  border: 4px solid light-dark(var(--color-border-light), var(--color-border-dark));
  background-color: transparent;
  color: light-dark(var(--color-text-light), var(--color-text-dark));

  // Text
  font-family: var(--font-headings);
}

// Navigation links
.nav__list-link {
  display: block;

  align-self: center;

  padding: 1rem var(--space-s-m);

  text-align: center;
  text-decoration: none;
}

.site-header .nav__list-link {
  font-weight: 500;
}

