.nav__list-item:not(:last-child) {
  margin-bottom: 0;
	margin-block-end: 0;
}

/* Change color on hover */
.nav__list-item:hover {
  background-color: light-dark(var(--color-bg-dark), var(--color-bg-light));
}

.nav__list-item a:hover {
  color: light-dark(var(--color-text-dark), var(--color-text-light));
  font-weight: 500;
}

// POST LISTS

.post-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

li.post-list__item {
  // Layout
  margin-block-end: 0; // Clear semantic list margins

  // Colors
  background-color: hsla(from var(--dklt-clr-bkg) 0 0 65 / 0.05);

  // Border
	border: 2px solid var(--ltdk-clr-bkg-bdr);
	border-left: 10px solid var(--ltdk-clr-bkg-bdr);
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;

  // Shadow
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.1);
}

.post-preview {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.post-img {
  flex: 0 1;
  aspect-ratio: 16/9;
}

.post-content {
  flex: 1 1;
  padding: 1.5em;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.post-meta {
  margin-block-start: 0.5em;
  font-size: var(--font-size-1);
}

.post__tags {
  font-size: var(--font-size-0);
}

.post__tags a {
  font-weight: 600;
}

.post-img img {
  width: 100%;
  object-fit: cover;
  border-top-right-radius: 1rem;
}

.post-link {
  color: inherit;
  // letter-spacing: 0.05em;
}

div.post-author {
  font-size: var(--font-size-1);
  margin-bottom: 0;
}

.post-author a {
  font-weight: 500;
  text-decoration: none;
}

.post-excerpt {
  margin: 0.5em 0;
  max-width: 60ch;
  // text-wrap: balance;
}

.post-date {
  font-size: var(--font-size-sm);
}

/* TAGS */

.post__tags {
  display: flex;
  flex-wrap: wrap;
  
  margin-top: 1rem;
  margin-block-start: 1rem;
}

.tag__link {
  display: inline-block;
  background-color: light-dark(var(--color-text-light), var(--color-text-dark));
  color: light-dark(var(--color-text-dark), var(--color-text-light));
  padding: 0.2em 0.5em;
  margin: 0.2em 0;
  border-radius: 5px;
  text-decoration: none;
}

/* SEARCH BAR */

.search-bar {
  margin-block-start: 0;
}

.search-input {
  padding: 1em;
  font-size: var(--font-size-0);
  border: 2px solid var(--ltdk-clr-txt);
  border-radius: 0.5rem;
  margin-block: 1em;
}

.search-results {
  list-style: none;
}

/* FEATURED IMAGE */

.featured-img {
  flex: 1 1 auto;
}

.featured-img--news {
  width: 100%;
  flex: 0 0 400px;

  margin-bottom: var(--space-s-l);
}

.featured-img--staff {
  flex: 0 0 300px;
  margin-bottom: var(--space-s-l);
}

.featured-img--project {
  flex: 0 0 600px;
  margin-bottom: var(--space-s-l);
}

.project-list__item--featured .project-title {
  margin-block-end: 0.25em;
}

.project-list__item--featured .project-excerpt {
  margin-block-end: 0;
}

/* SLIDER */

.slider {
  overflow: hidden;
}

.slider-img {
  border: solid 2px light-dark(var(--color-bg-border-light), var(--color-bg-border-dark));

  color: var(--color-text-light);
  font-size: var(--font-size-1);
}

.slider-btns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.75rem;
  margin-block-start: 0.75rem;
  margin-block-end: 0.5em;
  height: 3rem;
  justify-content: space-between;
}

.slider-btn {
  border: solid 2px light-dark(var(--color-bg-border-light), var(--color-bg-border-dark));

  color: var(--ltdk-clr-txt);
  background-color: light-dark(var(--color-bg-light), var(--color-bg-dark));

  font-size: var(--font-size-1);
}
