/*  ~~~~~~~~~~~~~
    ~~~ Color ~~~
    ~~~~~~~~~~~~~  */

:root {
  // Activate light and dark mode
  color-scheme: dark light;

  // Brand specific
  --color-brand-darkblue: #293558;
  --color-brand-primary: #293558;
  --color-brand-dark: #293558;
  --color-brand-bg-dark: #293558;
  //
  --color-brand-paleblue: #566db0;
  --color-brand-primary-shade: #566db0;
  --color-brand-secondary: #566db0;
  //
  --color-brand-yellow: #ffce32;
  --color-brand-accent: #ffce32;
  --color-brand-tertiary: #ffce32;
  //
  --color-brand-darkgray: #4f4f4f;
  --color-brand-darkgrey: #4f4f4f;
  //
  --color-brand-lightgray: #d4d7de;
  --color-brand-lightgrey: #d4d7de;
  --color-brand-text: #d4d7de;
  --color-brand-light: #d4d7de;
    //
  --color-brand-white: #f9f9f9;
  --color-brand-bg-light: #f9f9f9;
  --color-brand-accent: #f9f9f9;

  // Optimized Nomenclature
  --clr-brand-darkblue: #293558;
  --clr-brand-primary: #293558;
  --clr-brand-dark: #293558;
  --clr-brand-bg-dark: #293558;
  //
  --clr-brand-paleblue: #566db0;
  --clr-brand-primary-shade: #566db0;
  --clr-brand-secondary: #566db0;
  --clr-txt-visited: #566db0;

  //
  --clr-brand-yellow: #ffce32;
  --clr-brand-accent: #ffce32;
  --clr-brand-tertiary: #ffce32;
  //
  --clr-brand-darkgray: #4f4f4f;
  --clr-brand-darkgrey: #4f4f4f;
  //
  --clr-brand-lightgray: #d4d7de;
  --clr-brand-lightgrey: #d4d7de;
  --clr-brand-text: #d4d7de;
  --clr-brand-light: #d4d7de;
    //
  --clr-brand-white: #f9f9f9;
  --clr-brand-bg-light: #f9f9f9;
}

// Light-dark specific
:root {
  // Light Mode colors
  --color-bg-light: #d4d7de;
  --color-bg-gradient-light: radial-gradient(#566db0 30%, #293558);
  --color-bg-border-light: #3e5085;
  --color-border-light: #293558;
  --color-highlight-light: #293558;
  --color-text-light: #293558;

  // Dark Mode colors
  --color-bg-dark: #293558;
  --color-bg-gradient-dark: radial-gradient(#293558 30%, #566db0);
  --color-bg-border-dark: #d4d7de;
  --color-border-dark: #d4d7de;
  --color-highlight-dark: #ffce32;
  --color-text-dark: #d4d7de;

  // Revised property names
  --ltmode-clr-bkg: #d4d7de;
  --ltmode-clr-bkg-grd: radial-gradient(#566db0 30%, #293558);
  --ltmode-clr-bkg-bdr: #3e5085;
  --ltmode-clr-bdr: #293558;
  --ltmode-clr-hlt: #566db0;
  --ltmode-clr-hlt-txt: #d4d7de;
  --ltmode-clr-txt: #293558;

  --dkmode-clr-bkg-: #293558;
  --dkmode-clr-bkg-grd: radial-gradient(#293558 30%, #566db0);
  --dkmode-clr-bkg-bdr: #d4d7de;
  --dkmode-clr-bdr: #d4d7de;
  --dkmode-clr-hlt: #ffce32;
  --dkmode-clr-hlt-txt: #293558;
  --dkmode-clr-txt: #d4d7de;

  --ltdk-clr-bkg: light-dark(#d4d7de, #293558);
  --ltdk-clr-bkg-bdr: light-dark(#293558, #d4d7de);
  --ltdk-clr-txt: light-dark(#293558, #d4d7de);
  --ltdk-clr-hlt: light-dark(#293558, #d4d7de);
  --ltdk-clr-hlt-txt: light-dark(#566db0, #ffce32);
  --ltdk-clr-hlt-txt-visited: #566db0;

  --dklt-clr-bkg: light-dark(#293558, #d4d7de);

}

/* <mark> with .highlight class */

mark.highlight {
  color: var(--ltdk-clr-hlt);
  background-color: var(--ltdk-clr-hlt-txt);
  font-style: normal;
}

/* SELECTION */

::selection {
  color: var(--clr-brand-dark);
  background-color: var(--clr-brand-accent);
}

