/* ~~~~~~~~~~~~~~~~~~~~~ *\ 
    Generic Page Styles
\* ~~~~~~~~~~~~~~~~~~~~~ */

.page__header {
  margin-bottom: 2rem;
  margin-block-end: 2rem;
}

.page__excerpt {
  font-family: var(--font-headings-narrow);
  font-weight: 350;
  font-size: var(--font-size-2);
  text-wrap: balance;
}

/*\ ~~~ Sections ~~~ \*/

/*\ All Pages \*/

.page__section {
  padding-top: 1rem; // Fallback
  padding-block-start: 1rem;

  border-top: 5px solid var(--ltdk-clr-txt);
}

.section__prelude {
  font-family: var(--font-headings-narrow);
  font-weight: 350;

  font-size: var(--font-size-2);
}

// .page__section:first-of-type {
//   margin-bottom: 3rem; // Fallback
//   margin-block-end: 3rem;  
// }

.page__section + .page__section {
  margin-top: 3rem; // Fallback
  margin-block-start: 3rem;
}

.page__section:not(:last-child) {
  padding-bottom: 3rem; // Fallback
  padding-block-end: 3rem;
}

.page__section--inner {
  margin-top: 5rem;
  margin-block-start: 5rem;
}

.page__section--inner h4 {

  padding-bottom: 1rem; // Fallback
  padding-block-end: 1rem;

  border-bottom: 3px solid var(--ltdk-clr-txt);

  font-weight: 450;
}

