.about-lists {
  list-style: none;
  text-wrap: balance;
  margin-top: 1rem;
  margin-block-start: 1rem;
}

.about-lists h3 {
  margin-top: 0;
  margin-block-start: 0;
}



/* ~~~~~~~~~~~~ *\ 
    News Page
\* ~~~~~~~~~~~~ */

.post-list {
  --min: 45ch;
  --gap: 1rem;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(min(100%, var(--min)), 1fr));
  gap: var(--gap);
}

.post-list__item {
  list-style: none;
  text-wrap: balance; // Firefox fallback
  text-wrap: pretty;
}





/* ~~~~~~~~~~~~~~~ *\ 
    Projects Page
\* ~~~~~~~~~~~~~~~ */

/* PROJECTS */

.project-list {
  --min: 45ch;
  --gap: 1rem;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(min(100%, var(--min)), 1fr));
  gap: var(--gap);

  margin-block-start: 2rem;

  list-style: none;
}

.project-list__item {
  list-style: none;
  text-wrap: balance; // Firefox fallback
  text-wrap: pretty;
}

.project-title {
  font-size: var(--font-size-2);
  margin: 0.5em 0 0;
}

.project-excerpt {
  margin-top: 0.25rem;
  text-wrap: balance; // Firefox fallback
  text-wrap: pretty;
}

.featured-projects .project-list  {
  --gap: 3em;
}



/* ~~~~~~~~~~~~ *\ 
    Staff Page
\* ~~~~~~~~~~~~ */

.staff-list {
  --min: 45ch;
  --gap: 2rem;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(min(100%, var(--min)), 1fr));
  gap: var(--gap);
}

.staff-list__item {
  list-style: none;
  text-wrap: balance; // Firefox fallback
  text-wrap: pretty;
}

.staff-credentials {
  font-size: 0.75em;
}
