/* HEADER Styles */

.home-header {
  width: 100%;
  padding: 4rem 1rem;
}

.site-header {
  // TODO: Uncomment when masking solution is found
  // position: sticky;
  // top: 0;

  padding: 2em 0;
  width: 100vw;
  
  // background-image: light-dark(
  //   var(--ltmode-clr-bkg-grd), var(--dkmode-clr-bkg-grd)
  // );
  // box-shadow: 0 8px 32px RGBA(0, 0, 0, 0.25);
  border-bottom: 8px solid var(--ltdk-clr-bkg-bdr);
}

.site-header :focus {
  outline-color: var(--color-dark);
}

.site-header a {
  text-decoration: none;
}

.site-title {  
  color: var(--ltdk-clr-txt);
}

// Logo

.logo {
  color: light-dark(var(--color-text-light), var(--color-text-dark));
  font-weight: bold;
  font-size: var(--font-size-4);
  text-align: center;
  object-fit: contain;

  @media (prefers-color-scheme: dark) {
    filter: drop-shadow(0.25rem 0.25rem 1rem light-dark(rgba(64, 64, 64, .5), rgba(0, 0, 0, .5)));
  }
}

.logo-home {
  width: 100%;
  max-width: 800px;

  font-size: var(--font-size-6);
	
}

.logo-site-header {
  max-width: 8rem;

  font-size: var(--font-size-4);
}

@media screen and (max-width: 750px) {
  .container {
    width: 85%;
  }
  .navbar {
    flex-direction: column;
    width: 100%;
  }
  .navbar a {
    width: 100%;
  }
}
