/*  ~~~~~~~~~~~~~  *\
    ~~~ Fonts ~~~
\*  ~~~~~~~~~~~~~  */

/* Project-specific fonts and text styles */

:root {

  /* ~~~~~~~~~~~~~~~~~~~ *\
      Font Sizes in REM 
  \* ~~~~~~~~~~~~~~~~~~~ */

  --font-size-xs: 0.65rem;
  --font-size-sm: 0.8rem;
  --font-size-md: 1rem;
  --font-size-lg: 1.25rem;
  --font-size-xl: 1.5rem;
  --font-size-2xl: 2rem;
  --font-size-3xl: 3rem;
  --font-size-4xl: 4rem;





  /* ~~~~~~~~~~~~~~~~~~~~~ *\
      Font Sizes in CLAMP 
  \* ~~~~~~~~~~~~~~~~~~~~~ */

  --font-size--2: clamp(0.6944rem, 0.6866rem + 0.0417vw, 0.72rem);
  --font-size--1: clamp(0.8333rem, 0.8129rem + 0.1088vw, 0.9rem);
  --font-size-0: clamp(1rem, 0.9617rem + 0.2041vw, 1.125rem);
  --font-size-1: clamp(1.2rem, 1.1369rem + 0.3367vw, 1.4063rem);
  --font-size-2: clamp(1.44rem, 1.3427rem + 0.5189vw, 1.7578rem);
  --font-size-3: clamp(1.728rem, 1.5843rem + 0.7661vw, 2.1973rem);
  --font-size-4: clamp(2.0736rem, 1.8676rem + 1.0987vw, 2.7466rem);
  --font-size-5: clamp(2.4883rem, 2.1991rem + 1.5427vw, 3.4332rem);
  --font-size-6: clamp(2.986rem, 2.5863rem + 2.1315vw, 4.2915rem);





  /* ~~~~~~~~~~~~~ *\
      Font Stacks 
  \* ~~~~~~~~~~~~~ */

  --font-sans: "Work Sans", "Roboto", "Segoe UI", "Helvetica Neue", Helvetica, Arial, sans-serif;
  --font-code: "Source Code Pro", "Space Mono" Menlo, Consolas, 'DejaVu Sans Mono', ui-monospace, monospace;
  --font-serif: "Source Serif 4", "Times New Roman", serif;
  --font-transitional: Charter, 'Bitstream Charter', 'Sitka Text', Cambria, serif;
  --font-headings: "Space Grotesk", "Recursive", "Helvetica Neue",
    Helvetica, Arial, sans-serif;
  --font-headings-alt: "Tomorrow";
  --font-headings-narrow: "Recursive", "Archivo Narrow", "Helvetica Neue", Helvetica, Arial,
    sans-serif;
  --font-system-ui: system-ui, sans-serif;
} 
