/* Margin */

.margin-block-start-0 {
  margin-block-start: 0;
}

.margin-block-end-half {
  margin-block-end: 0.5em;
}

.margin-inline-start--auto {
  /* Pushes content to the right edge of container */
  margin-inline-start: auto;
}

.margin-inline-end--auto {
  /* Pushes content to the left edge of container */
  margin-inline-end: auto;
}

/* Padding */

.padding-block-end-sm {
  padding-block-end: 1em;
}

.padding-block-end-md {
  padding-block-end: 2em;
}

.padding-block-end-lg {
  padding-block-end: 3em;
}

.padding-inline-start--auto {
  /* Pushes content to the right edge of container */
  padding-inline-start: auto;
}

.padding-inline-end--auto {
  /* Pushes content to the left edge of container */
  padding-inline-end: auto;
}

/* Text Alignment */

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

/* ~~~~~~~~~~~~~~~ */
/* ~~~ FLEXBOX ~~~ */
/* ~~~~~~~~~~~~~~~ */

.flex {
  display: flex;
}

/* Flex Options */

.flex-wrap {
  flex-wrap: wrap;
}

.flex-grow {
  flex-grow: 1;
}

/* Flex Direction */

.flex-row {
  flex-direction: row;
}

.flex-row-reverse {
  flex-direction: row-reverse;
}

.flex-column {
  flex-direction: column;
}

.flex-column-reverse {
  flex-direction: column;
}

/* Flex Spacing */

.flex-space-between {
  justify-content: space-between;
}

.flex-space-around {
  justify-content: space-around;
}

/* Flex Positioning */

.flex-start-x {
  justify-content: flex-start;
}

.flex-start-y {
  align-items: flex-start;
}

.flex-center-x {
  justify-content: center;
}

.flex-center-y {
  align-items: center;
}

.flex-center-xy {
  justify-content: center;
  align-items: center;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-end-x {
  justify-content: flex-end;
}

.flex-end-y {
  align-items: flex-end;
}

/* ~~~~~~~~~~~~ */
/* ~~~ GRID ~~~ */
/* ~~~~~~~~~~~~ */

.grid {
  display: grid;
}

.grid-item {
  border: 2px solid var(--color-border);
  padding: 2em;
  text-wrap: balance;
}

.grid-item h3 {
  font-family: var(--font-headings-narrow);
}

.grid-item h3,
.grid-item p {
  margin: 0 auto;
}

.grid-item p {
  margin-block-start: 1em;
}

.item1 {
  grid-area: item1;
}
.item2 {
  grid-area: item2;
}
.item3 {
  grid-area: item3;
}
.item4 {
  grid-area: item4;
}
.item5 {
  grid-area: item5;
}
.item6 {
  grid-area: item6;
}

/* Grid Patterns */

.grid--2x2 {
  display: grid;
  grid-template-areas:
    "item1 item2"
    "item3 item4";
  grid-template-columns: 1fr 1fr;
}

.grid--2x3 {
  display: grid;
  grid-template-areas:
    "item1 item2 item3"
    "item4 item5 item6";
  grid-gap: 1em;
  grid-template-columns: 1fr 1fr;
}

/* Grid Columns */

.grid--col-2 {
  grid-template-columns: repeat(2, 1fr);
}
.grid--col-3 {
  grid-template-columns: repeat(3, 1fr);
}
.grid--col-4 {
  grid-template-columns: repeat(4, 1fr);
}
.grid--col-5 {
  grid-template-columns: repeat(5, 1fr);
}
.grid--col-6 {
  grid-template-columns: repeat(6, 1fr);
}

/* Grid Gap */

.gap-xs {
  gap: 0.5em;
}

.gap-sm {
  gap: 1em;
}

.gap-md {
  gap: 2em;
}

.gap-lg {
  gap: 4em;
}

/* Grid Centering */

.grid-center {
  place-items: center;
}

/* ~~~~~~~~~~~~~ */
/* ~~~ BLOCK ~~~ */
/* ~~~~~~~~~~~~~ */

/* Centering without Flex or Grid */

.center-horizontal {
  /* Center Horizontal-Only */
  margin: 0 auto;
}

.center-vertical {
  /* Center Vertical-Only */
  display: block;
  align-content: center;
}

/* Place Children */

.place-end {
  place-items: end;
}

/* Place Self */

.self--start {
  place-self: start;
}

.self--center {
  place-self: center;
}

.self--end {
  place-self: end;
}

/* ~~~~~~~~~~~~~~~~~~~~~~~~~~ */
/* ~~~ GAP [Flex or Grid] ~~~ */
/* ~~~~~~~~~~~~~~~~~~~~~~~~~~ */

.gap-xxs {
  gap: 0.5rem;
}

.gap-xs {
  gap: 1rem;
}

.gap-sm {
  gap: 2rem;
}

.gap-md {
  gap: 4rem;
}

.gap-lg {
  gap: 6rem;
}

.gap-xl {
  gap: 8rem;
}

.gap-xxl {
  gap: 10rem;
}

/* ~~~~~~~~~~~~~ */
/* ~~~ FONTS ~~~ */
/* ~~~~~~~~~~~~~ */

/* ~~~ Font sizes based on root ~~~ */

.font-xxs {
  font-size: 0.5rem;
}

.font-xs {
  font-size: 0.625rem;
}

.font-s {
  font-size: 0.75rem;
}

.font-s-md {
  font-size: 0.875rem;
}

.font-md {
  font-size: 1rem;
}

.font-md-lg {
  font-size: 1.25rem;
}

.font-lg {
  font-size: 1.5rem;
}

.font-xl {
  font-size: 2rem;
}

.font-xxl {
  font-size: 3rem;
}

/* ~~~~~~~~~~~~~~~~~~~~~~~ */
/* ~~~ IMAGE UTILITIES ~~~ */
/* ~~~~~~~~~~~~~~~~~~~~~~~ */

/* ~~~ Transparency ~~~ */

.transparent {
  background-color: transparent;
  /* Use for PNG and SVG files. */
}

/* ~~~ Aspect Ratios ~~~ */

.aspect-1x1 {
  aspect-ratio: 1/1;
}

.aspect-3x2--portrait {
  aspect-ratio: 2/3;
}

.aspect-3x2--landscape {
  aspect-ratio: 3/2;
}

.aspect-5x3--portrait {
  aspect-ratio: 3/5;
}

.aspect-5x3--landscape {
  aspect-ratio: 5/3;
}

.aspect-16x9--portrait {
  aspect-ratio: 9/16;
}

.aspect-16x9--landscape {
  aspect-ratio: 16/9;
}

