/* Burger Menu Styles */

.burger-menu__trigger {
  display: none;

  /*
  If the component is disabled—which is our default state—we don’t want to present a trigger. Hiding it with display: none will hide it from screen readers, too.
  */
}

.burger-menu__bar,
.burger-menu__bar::before,
.burger-menu__bar::after {
  display: block;
  width: 24px;
  height: 3px;
  background: light-dark(var(--color-text-light), var(--color-text-dark));
  position: absolute;
  left: 50%;
  margin-inline-start: -12px;

  /*
  Style the innerHTML bars.
  */
}

.burger-menu__bar {
  top: 50%;
  transition: background 10ms 300ms ease;
  transform: translateY(-50%);
  /*
  Position the .burger-menu__bar dead-center with absolute positioning, which allows us to comfortably animate it, knowing it won’t affect layout.
  */
}

/* Render the pseudo-elements and push one up and one down. */

.burger-menu__bar::before,
.burger-menu__bar::after {
  content: "";
}

.burger-menu__bar::before {
  transition: top 300ms 350ms ease, transform 300ms 50ms ease;
  top: -8px;
}

.burger-menu__bar::after {
  transition: bottom 300ms 350ms ease, transform 300ms 50ms ease;
  bottom: -8px;
}

/* ~~~ CSS Exceptions ~~~ */

/* Handle styling of the menu when it is enabled */

.burger-menu[enabled="true"] .burger-menu__trigger {
  display: block;

  // Enable stacking context
  position: relative;
  z-index: 999;

  // Nice big tap target
  width: 2rem;
  height: 2rem; 

  background: transparent;
  border: none;
  cursor: pointer;
}

.burger-menu[enabled="true"] .burger-menu__panel {
  /* Hide panel visibility when menu is closed to allow user interaction with other page elements "below" the panel. */
  visibility: hidden;
  opacity: 0;

  background-color: var(--ltdk-clr-bkg-bdr);
  
  // Position the panel & enable stacking context
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1; /* We increase the z-index to ensure the modal menu covers all other content except the window border. */

  // Make panel fill the screen.
  width: 100%;
  height: 100%;

  // Style interior content
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5rem 1.5rem 2rem;


  /* Set the vertical overflow to auto so long menus can be scrolled. */
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  .nav--primary .nav__list-item {
    border: 4px solid var(--ltdk-clr-bkg);
    color: var(--ltdk-clr-bkg);
  }

  .nav--primary .nav__list-item .active {
    background-color: var(--ltdk-clr-bkg);
    color: var(--ltdk-clr-bkg-bdr);
  }

  .nav--primary .nav__list-item :hover {
	  background-color: var(--ltdk-clr-bkg);
    color: var(--ltdk-clr-bkg-bdr);
  }
}

/* Handle the logo styling */

.logo--burger-menu {
  max-width: 400px;
  margin-block-end: 3rem;
}

.burger-menu[enabled="true"] .logo--burger-menu {
  visibility: hidden;
  opacity: 0;
}

.burger-menu[enabled="true"][status="open"] .logo--burger-menu {
  visibility: visible;
  opacity: 1;
}

.burger-menu[enabled="true"][status="closed"] .logo--burger-menu {
  visibility: hidden;
  opacity: 0;
}

/* Convert the navigation into a stacked menu when the burger menu is enabled. */

.burger-menu[enabled="true"] .nav ul {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.burger-menu[enabled="true"] .nav li {
  font-size: 1.5rem;
}

/* Hide the navigation list items until the burger menu is open. */

.burger-menu[enabled="true"][status="closed"] .nav li {
  opacity: 0;
}

.burger-menu[enabled="true"][status="open"] .nav li {
  opacity: 1;
}

/* Interactive States */
/* The panel’s visibility can only be changed if the burger menu is enabled and the status is open. */

.burger-menu[enabled="true"][status="open"] .burger-menu__panel {
  /* When the panel is “open”, we transition the opacity to 1 and set visibility to visible to show it. */
  visibility: visible;
  transition: opacity 400ms ease;
  opacity: 1;
}

.burger-menu[enabled="true"][status="closed"] .burger-menu__panel {
  transition: opacity 400ms ease;

  /* In the “closed” state, we transition the navigation panel to its default opacity (i.e. 0). */
}

.burger-menu[enabled="true"][status="closed"] .burger-menu__panel > * {
  transition: opacity 400ms ease;
  opacity: 0;

  /* In the “closed” state, we visually hide the navigation items with opacity. */
}

.burger-menu[enabled="true"][status="open"] .burger-menu__panel > * {
  transition: 
    transform 500ms cubic-bezier(0.17, 0.67, 0, 0.87) 400ms,
    opacity 500ms ease 400ms;
  transform: translateY(0);
  opacity: 1;
  
  /* When the panel is in the “open” state, we transition them back to being visible with full opacity. */
}

/* 
BURGER MENU BAR ANIMATION

Convert burger bars into a 'close' icon when the menu is open. 

Sets the background and border of the main (central) bar to be transparent, and rotates the pseudo-elements in opposite directions to create a cross.
*/

.burger-menu[enabled="true"][status="open"] .burger-menu__bar::before {
  transition: top 300ms 50ms ease, transform 300ms 350ms ease, background 300ms 50ms ease;
  top: 0;
  transform: rotate(45deg);
  background: var(--ltdk-clr-bkg);
}

.burger-menu[enabled="true"][status="open"] .burger-menu__bar::after {
  transition: bottom 300ms 50ms ease, transform 300ms 350ms ease, background 300ms 50ms ease;
  bottom: 0;
  transform: rotate(-45deg);
  background: var(--ltdk-clr-bkg);
}

.burger-menu[enabled="true"][status="open"] .burger-menu__bar {
  background: transparent;
}
