html {
  height: 100%;
}

.body__foreground {
  min-height: 100%;

  display: grid;
  grid-template-rows: auto 1fr auto;
}

.flex-columns {
  --min: 45ch;
  --gap: 1rem;

  display: flex;
  flex-wrap: wrap;
  gap: var(--gap);
}

.flex-columns > * {
  flex: 1 1 var(--min);
}

.grid-columns {
  --min: 45ch;
  --gap: 1rem;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(min(100%, var(--min)), 1fr));
  gap: var(--gap);
}

.grid-columns > * {
  list-style: none;
  text-wrap: balance; // Firefox fallback
  text-wrap: pretty;
}