/*  ~~~ CONTAINERS ~~~  */

// All containers must use the .container class.
.container {
  width: 90%;
  max-width: 1600px;
}

// Used for the site banner / site <header> element.
.container--site-header {
  margin: 0 auto;
}

// Used for the <main> element.
.container--main {
  margin: 4rem auto 0;
  gap: 2rem;
}

// Used for the site banner / site <header> element.
.container--site-footer {
  display: flex;
  margin: 1em auto;
}

/*  ~~~ WRAPPERS ~~~  */

.wrapper {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

.wrapper--home-banner {
  display: flex;
  align-items: center;
  place-self: center;
}

.wrapper--lg-screens {
  max-width: 100rem;
  margin-inline-start: auto;
  margin-inline-end: auto;

  /* We specifically add left/right padding/margin here so other compositional CSS can comfortably manage vertical space, if needed. 

  padding-inline-start: 1.25rem;
  padding-inline-end: 1.25rem;

  */
}

@media (min-height: 940px) {
  .wrapper--home-banner {
    height: 100vh;
    height: 100lvh;
  }
}