.background-border {
  // Position and enable stacking context
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;

  width: 100vw;
  height: 100%;
  // height: 100svh;
  // height: 100dvh;
  
  border: 8px solid var(--ltdk-clr-bkg-bdr);

  pointer-events: none;
}

.background-gradient {
  // Position and enable stacking context
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;

  width: 100vw;

  height: 100vh; // Fallback for all screens
  height: 100svh; // Fallback for small screens
  height: 100dvh;

  pointer-events: none;

}

h1 {
  color: light-dark(var(--ltmode-clr-txt), #d4d7de);
}

.light-dark-panel {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding-top: 1rem;
  padding-block-start: 1rem;
}

legend {
  width: 100%;
  text-align: center;
}

input[type="radio"] {
  appearance: none;
  position: absolute;
  width: 0;
  height: 0;
}

label {
  border: 2px solid light-dark(#293558, #d4d7de);
  border-radius: 0.5rem;
  display: grid;
  place-items: center;
  padding: 10px;
  aspect-ratio: 1 / 1;
  width: 80px;
}
  
label:hover {
  color: light-dark(var(--color-text-dark), var(--color-text-light));
  background-color: light-dark(#293558, #d4d7de);
}

label .icon {
  font-size: 1.5rem;
}

label .label-text {
  // Animation
  -moz-transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
  -webkit-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
}

input[type="radio"]:checked + label {
  border: 4px solid light-dark(#293558, #d4d7de);
}

/* Light and Dark Mode Preferences */

@media (prefers-color-scheme: light) {
  .background-gradient {
    background-image: var(--color-bg-gradient-light);
    mix-blend-mode: hard-light; /* Not functional at this time */
    opacity: 0.5;
  }
}

@media (prefers-color-scheme: dark) {
  .background-gradient {
    background-image: var(--color-bg-gradient-dark);
    mix-blend-mode: difference; /* Not functional at this time */
    opacity: 0.25;
  }
}