/* 
~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
~~~ HTML Semantic Elements ~~~
~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */

/*  ~~ Defaults ~~  */
html {
  font-family: var(--font-sans);
}

body {
  color: var(--ltdk-clr-txt);
  font-weight: 400;
  line-height: 1.6;
}

h1 + p,
h2 + p,
h3 + p,
h4 + p,
h5 + p,
h6 + p {
  margin-top: none;
  margin-block-start: none;
}

/*  ~~ Headings ~~  */

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-headings);
  line-height: 1;
}

h1 {
  font-family: var(--font-headings-alt);
  text-transform: uppercase;
  margin-block-end: 0.25em;
  font-size: var(--font-size-6);
  font-weight: 400;
}

h2 {
  font-family: var(--font-headings);
  // margin-block-start: 1em;
  margin-block-end: 0.25em;
  font-size: var(--font-size-5);
  font-weight: 400;
}

h3 {
  font-family: var(--font-headings-alt);
  margin-block-start: 0.5em;
  margin-block-end: 0.25em;
  font-size: var(--font-size-3);
  font-weight: 400;
  line-height: 1.2;
}

h4 {
  font-family: var(--font-headings);
  font-size: var(--font-size-2);
  font-weight: 400;
  margin-block-start: 2rem;
  line-height: var(--font-size-2);
}

h5 {
  font-family: var(--font-headings-alt);
  font-size: var(--font-size-1);
  font-weight: 400;
  line-height: var(--font-size-3);
}

h6 {
  font-size: var(--font-size-0);
  font-weight: 300;
  line-height: var(--font-size-0);
}

/* ~~~ Paragraphs ~~~ */

p {
  font-size: var(--font-size-0);
  font-weight: 400;
  // letter-spacing: 0.5px;
}

p:not(:last-child) {
  margin-bottom: 1rem;
  margin-block-end: 1rem;
}

/* ~~~ Lists ~~~ */

ul,
ol {
  font-family: var(--font-sans);
}

li {
  font-family: var(--font-sans);
}

li:not(:last-child) {
  margin-bottom: 1rem;
  margin-block-end: 1rem;
}

/*  ~~ Code Blocks & Preformatted Text ~~  */

code {
  font-family: var(--font-code);
  background-color: var(--color-code-background);
  padding: 0;
  padding-block-start: 0.2em;
  padding-block-end: 0.2em;
  margin: 0;
  font-size: 85%;
  border-radius: 3px;
}

code:before,
code:after {
  letter-spacing: -0.2em;
  content: "\00a0";
}

pre > code {
  padding: 0;
  margin: 0;
  font-size: 100%;
  word-break: normal;
  white-space: pre;
  background: transparent;
  border: 0;
}

pre {
  padding: 1rem;
  overflow: auto;
  font-size: 85%;
  line-height: 1.45;
  border-radius: 3px;
  background-color: #f7f7f7;
  margin-block-end: 1rem;
  word-break: normal;
  word-wrap: normal;
}

pre code {
  display: inline;
  max-width: initial;
  padding: 0;
  margin: 0;
  overflow: initial;
  line-height: inherit;
  word-wrap: normal;
  background-color: transparent;
  border: 0;
}

pre code:before,
pre code:after {
  content: normal;
}

/*  ~~ Horizontal Rule ~~  */
/* Determine best practice between border styling vs. background styling. */

/* Background styling */
hr {
  border: 0;
  clear: both;
  display: block;
  // width: 96%;
  background-color: var(--ltdk-clr-txt);
  height: 2px;
}

/* Border styling */
/* hr {
  border: 1px solid black;
} */ /*  */

/*  ~~ Hyperlinks ~~  */
a {
  color: currentColor;
  font-weight: 400;
  text-decoration-thickness: 2px;
}

a:hover,
a:hover img {
  color: var(--ltdk-clr-hlt-txt);
}


/*  ~~ Blockquotes ~~  */

blockquote {
  background: #ddd;
  border-left: 10px solid #aaa;
  margin: 1.5em 0;
  padding: 1em 2em;
  font-style: italic;
  quotes: "\201C""\201D""\2018""\2019";
}

blockquote::before {
  color: #aaa;
  font-family: var(--font-headings);
  font-style: normal;
  content: "\201C";
  font-size: 12rem;
  line-height: 0.1rem;
  margin-inline-end: 0.25em;
  vertical-align: -0.5em;
}

blockquote::before {
  content: "\201C";
}

blockquote p {
  font-family: var(--font-serif);
  font-size: var(--font-size-1);
}

/*  ~~~ IMAGES ~~~  */

/* This will set a basic display when an image is not available. */
img {
  color: var(--color-text-light);
  background-color: #bcbcbc;
  display: grid;
  place-items: center;
  object-fit: contain;

  font-size: var(--font-size-1);
}

/*  ~~ PAGE LAYOUT ~~  */

body {
  margin-inline-start: auto;
  margin-inline-end: auto;
  min-height: 100vh;
  min-height: 100svh;
  min-height: 100dvh;
}

/* Images */

img {
  max-width: 100%;
  height: 100%;
  vertical-align: middle;
  font-family: var(--font-sans);
}

/* HEADER Styles */

header img {
  font-family: "Alfa Slab One";
  font-size: var(--font-size-3);
}

/* Navigation */

nav ul,
nav menu {
  margin: 0;
  padding: 0;
  list-style: none;
  list-style-type: none;
}

// nav ul li,
// nav menu li {
//   margin: 0 0.1rem; /* For browsers that do not support gap, we provide a fallback by adding a tiny, 0.1rem margin on all sides of a list item. */
// }

header nav ul li,
header nav menu li {
  font-size: var(--font-size-2);
}

/* MAIN Styles */

main {
  width: 100%;
}

main:focus {
  outline: none;

  /* We remove focus styles from the <main> element because when someone activates the skip link, it programmatically focuses the <main> because it’s the :target. The focus ring is unnecessary though, because making the <main> focusable, programmatically, is purely for making tabbing on the keyboard more predictable for users who want to skip navigation. */
}

/* SECTIONS */

/* section {
  padding: 8em 0;
} */

/* For ::selection styles, see brand-colors.scss */

/* ~~~ TABLES ~~~ */

table {
  border-collapse: collapse;
  border-spacing: 0;
  margin-block-end: 1rem;
}

td,
th {
  padding: 0;
}

tr,
td {
  border: 0;
}