/* ~~~~~~~~~~~~~~~~~~~~~~ */
/* ~~~ Footer Template ~~~ */
/* ~~~~~~~~~~~~~~~~~~~~~~ */

/* FOOTER Styles */

footer {
  width: 100%;

  margin-top: auto; // Fallback
  margin-block-start: 4rem;
  padding: 1.5em 2em;
  padding: 2em 0;

  border-top: 8px solid var(--ltdk-clr-txt);


  font-size: var(--font-size-0);

  color: var(--ltdk-clr-txt);

  // justify-content: center;
}

footer :is(h1, h2, h3, h4, h5, h6, p, a, ul, ol, li) {
  // color: var(--color-light);
  // color: var(--ltdk-clr-txt);
  font-family: var(--font-headings);
  font-weight: 400;
  font-style: normal;
}

footer a {
  text-decoration: none;
}

footer a:hover {
  color: light-dark(var(--color-text-light), var(--color-brand-secondary));
}

.footer--contact {
  width: 30%;
  max-width: 500px;
}

.footer--nav {
  max-width: 30rem;
  display: flex;
}

.footer--nav > ul {
  width: 600px;
  flex-wrap: wrap;
}

/* EXISTING STYLES BEGIN HERE */

footer ul {
  margin-block-end: 0;
}


/* ~~~ Footer Masthead ~~~ */

.masthead {
  place-items: end;
}

.logo-container {
  max-width: 100%;
  max-height: 100%;
  place-items: end;
}

#site-logo__footer {
  width: 300px;
}

footer address,
.contact-info {
  font-family: var(--font-headings);
  font-style: normal;
  font-size: 1.25rem;
  letter-spacing: 0.02em;
}

.contact-info span {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 1.25rem;
}

@media screen and (max-width: 750px) {

  .container--site-footer {
    flex-direction: column;
  }

  .footer-links {
    justify-content: center;
  }

  .footer-links ul {
    text-align: center;
  }

  .masthead {
    flex-direction: column;
    align-items: center;    
  }

  .contact-info {
    text-align: center;
  }

}