section.img-card {
  height: auto;
  padding: 3em;
  background-color: #efefef;
  border-radius: 1em;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 16px 20px 0 rgba(0, 0, 0, 0.19);
}

.img-card .flex-container {
  justify-content: center;
  align-items: center;
}

.img-card--row .flex-container {
  flex-direction: row;
}

.img-card--row h3,
.img-card--row p {
  text-align: right;
}

.img-card--row-reverse h3,
.img-card--row-reverse p {
  text-align: left;
}

.img-card--row-reverse .flex-container {
  flex-direction: row-reverse;
}

.flex-container {
  display: flex;
  gap: 5rem;
}

.img-card h3 {
  font-size: 1.5rem;
  margin: 0;
}

.img-card p {
  margin-block-end: 0;
}

.img-card hr {
  border: 1px solid black;
}

.img-card img {
  background-color: #bcbcbc;
  display: grid;
  place-items: center;
  width: 400px;
  height: 300px;
  object-fit: contain;
  border: 2px solid black;
}

.img-card--inner {
  gap: 2em;
}

.img-card .txt-wrapper {
  width: 30ch;
}

.img-card .img-wrapper {
  flex: 0 0 400px;
}

@media only screen and (max-width: 1000px) {
  .img-card--row .flex-container {
    flex-direction: column;
  }

  .img-card--row-reverse .flex-container {
    flex-direction: column-reverse;
  }

  .img-card h3,
  .img-card p {
    text-align: left;
  }

  .img-card--inner {
    gap: 2em;
  }

  .txt-wrapper {
    width: 400px;
  }

  .img-wrapper {
    flex-basis: 300px;
  }
}
